body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.layflex {
    height: 100vh;
}
.header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.logo-container {
    display: flex;
    flex-direction: row;
    font-size: 1.5rem;
    img {
        padding: 10px;
        width: 64px;
    }
    .logo {
        color: white;
    }
}

.charts-flex {
    text-align: center;
    width: 100%;
    margin: 10px;
    height: 400px;
    padding-right: 20px;
    padding-bottom: 40px;
}

.info-flex > p {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.info-flex {
    padding: 15px;
    background-color: snow;
    margin: 10px;
}

.dashboard-info-container {
    display: flex;
    flex-direction: row;
    margin: 10px;
}
.sub-flex {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 15px;
}

.info-span {
    font-weight: bold;
}

.container {
    margin: 40px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
}

.test-name {
    color: blueviolet;
}

.login-page {
    display: flex;
    justify-content: center;
    padding: 100px;
}

.login-container {
    display: flex;
    flex-direction: column;
}

.card-form {
    padding: 50px;
    text-align: center;
    flex-direction: column;
}

.card-form img {
    width: 250px;
    padding: 50px;
}

.ant-card {
    box-shadow: 0 0.5em 1em -0.125em rgba(10, 10, 10, 0.1), 0 0 0 1px rgba(10, 10, 10, 0.02);
}

.shadow-flex {
    box-shadow: 0 0.5em 1em -0.125em rgba(10, 10, 10, 0.1), 0 0 0 1px rgba(10, 10, 10, 0.02);
}

.react-json {
    color: snow;
}

.account {
    color: snow;
}
$markdown_font_size: 3rem;

.quark-documentation {
    font-family: "Roboto", "Helvetica", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    h1 {
        font-size: calc(#{$markdown_font_size - 0.3rem});
    }
    h2 {
        font-size: calc(#{$markdown_font_size - 0.6rem});
    }
    h3 {
        font-size: calc(#{$markdown_font_size - 0.9rem});
    }
    h4 {
        font-size: calc(#{$markdown_font_size - 1.2rem});
    }
    h5 {
        font-size: calc(#{$markdown_font_size - 1.5rem});
    }
    h6 {
        font-size: calc(#{$markdown_font_size - 1.8rem});
    }
    p {
        font-size: calc(#{$markdown_font_size - 1.9rem});
    }
}
